html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

html {
  height: 100%;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

@font-face {
  font-family: MoseratT;
  src: url("../fonts/Montserrat-Regular.ttf");
  font-weight: regular;
  font-style: normal;
}
@font-face {
  font-family: MoseratT;
  src: url("../fonts/Montserrat-SemiBold.ttf");
  font-weight: bold;
  font-style: normal;
}
html {
  font-family: "MoseratT";
  font-weight: regular;
}

.wrapper-icon {
  transition: transform 0.3s ease;
}
.wrapper-icon:hover {
  transform: translateX(-6px);
  animation: shake 0.5s infinite;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(-8px);
  }
  100% {
    transform: translateX(0);
  }
}
.container {
  max-width: 1140px;
  margin: 0 auto;
}

.section {
  position: relative;
  overflow: hidden;
}

.main-section {
  min-height: 800px;
  background: #F0F0F0;
  padding-top: 16px;
}
.main-section__nav-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 69px;
  align-items: center;
}
.main-section__logo {
  width: 134px;
}
.main-section__content-container {
  position: relative;
  display: flex;
  flex-direction: row;
}
.main-section__circle {
  position: absolute;
  top: 129px;
  right: 80px;
}
.main-section__second-circle {
  position: absolute;
  right: -120px;
  top: 650px;
}

.buttons__main-btn:hover {
  -webkit-animation: 0.4s shake;
  animation: 0.4s shake;
}

.nav-menu__list {
  display: flex;
  flex-direction: row;
}
.nav-menu__list-item {
  margin-right: 64px;
  font-size: 18px;
}
.nav-menu__list-item:last-child {
  margin-right: 0px;
}
.nav-menu a {
  text-decoration: none;
  color: black;
  font-weight: 800;
}

.lfet-wrapper {
  position: relative;
}
.lfet-wrapper img {
  position: relative;
  z-index: 5;
}
.lfet-wrapper__rectangle-blue {
  position: absolute;
  z-index: 2;
  top: 150px;
  left: -50px;
  width: 565.95px;
  height: 220px;
  background: #5290EA;
  border-radius: 100px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  transform: rotate(-64deg);
}
.lfet-wrapper__rectangle-orange {
  position: absolute;
  z-index: 3;
  left: -30px;
  top: 160px;
  width: 618.43px;
  height: 220px;
  background: #F28B64;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 100px;
  transform: rotate(-50.81deg);
}
.lfet-wrapper__rectangle-grey {
  position: absolute;
  z-index: 1;
  width: 608.97px;
  height: 220px;
  left: 10px;
  top: 190px;
  background: #E2E2E2;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 100px;
  transform: rotate(-36.15deg);
}

.right-wrapper {
  width: 372px;
  position: absolute;
  top: 150px;
  right: 76px;
  z-index: 5;
}
.right-wrapper__icons-box {
  margin-bottom: 1px;
  padding-left: 10px;
}
.right-wrapper__title {
  margin-bottom: 5px;
}
.right-wrapper__text {
  font-size: 16px;
  margin-bottom: 20px;
}

.icons {
  display: inline-flex;
}
.icons__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border: 1px solid #5290EA;
  border-radius: 100px;
  margin-right: 17px;
}
.icons__item:last-child {
  margin-right: 0px;
}
.icons i {
  font-size: 21px;
}

.title {
  font-size: 72px;
  position: relative;
  font-weight: bold;
}
.title__hr {
  width: 65px;
  position: absolute;
  margin: 0;
  border: none;
  border-top: 1px solid black;
}

.buttons {
  display: inline-flex;
}
.buttons__main-btn {
  margin-right: 32px;
  width: 200px;
  height: 60px;
  background: #F28B64;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 100px;
  border: none;
  color: white;
  font-size: 18px;
}
.buttons__icon-btn {
  display: inline-flex;
  align-items: center;
}
.buttons__text {
  font-size: 18px;
}
.buttons img {
  margin-right: 12px;
}

.circle {
  width: 194px;
  height: 194px;
  background: linear-gradient(143.92deg, #ED7B50 14.85%, #488AEA 53.25%, #2C2C2C 85.74%);
  border-radius: 170px;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 50%;
  animation: rotate 10s linear infinite;
}
.circle_big {
  width: 282px;
  height: 282px;
}
.circle__subtract {
  width: 129.33px;
  height: 129.33px;
  background: #F0F0F0;
  border-radius: 170px;
}
.circle__subtract_big {
  width: 188px;
  height: 188px;
}

.second-section {
  min-height: 700px;
}
.second-section__container {
  position: relative;
  padding-top: 32px;
}
.second-section__title {
  margin-bottom: 40px;
}
.second-section__cards-box {
  display: flex;
  margin-bottom: 25px;
}
.second-section__cards-item {
  margin-right: 30px;
}
.second-section__cards-item:last-child {
  margin-right: 0px;
}
.second-section__icon-box {
  display: flex;
  justify-content: space-evenly;
}
.second-section__circle {
  position: absolute;
  top: 80px;
  right: -60px;
  z-index: -1;
}
.second-section__second-circle {
  position: absolute;
  top: 344px;
  right: 960px;
  z-index: -1;
}

.upper {
  font-size: 40px;
  font-weight: bold;
}

.icon-item {
  display: flex;
  align-items: center;
}
.icon-item__img {
  margin-right: 20px;
  width: 80px;
}
.icon-item__text {
  font-size: 18px;
}

.cards-item {
  position: relative;
  width: 360px;
  height: 360px;
  background: #E2E2E2;
  border-radius: 10px;
  display: flex;
  align-items: flex-end;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.cards-item_orange {
  background-color: #F28B64;
}
.cards-item_blue {
  background-color: #5290EA;
}
.cards-item__img {
  height: 335px;
}
.cards-item__text {
  min-width: 180px;
  font-size: 36px;
  font-weight: bold;
  text-align: center;
  transform: rotate(90deg);
  position: absolute;
  top: 50%;
  left: 50%;
}

.third-section {
  min-height: 640px;
  background: #F0F0F0;
  position: relative;
}
.third-section__container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding-left: 150px;
  padding-top: 46px;
  position: relative;
  margin-bottom: 50px;
}
.third-section__left-title {
  z-index: 5;
}
.third-section__right-wrapper {
  max-width: 480px;
  padding: 50px;
  border: 1.9777px solid rgba(153, 153, 153, 0.8078431373);
  border-radius: 10%;
}
.third-section__right-title {
  margin-bottom: 20px;
  z-index: 5;
}
.third-section__text {
  margin-bottom: 30px;
  font-size: 18px;
}
.third-section__img {
  position: absolute;
  z-index: 0;
  top: 270px;
  left: -10px;
  animation: rotate 30s linear infinite;
}
.third-section__circle {
  position: absolute;
  z-index: 2;
  left: 150px;
  top: 220px;
  scale: 1.4;
}
.third-section__second-circle {
  position: absolute;
  top: -120px;
  right: -20%;
  z-index: 0;
  scale: 2;
}

.title__hr_top {
  bottom: 75px;
}

.footer__img {
  position: absolute;
  left: -25px;
  bottom: bottom;
}
.footer__container {
  min-height: 272px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.footer__title {
  margin-bottom: 20px;
}

.footer_icons {
  padding: 0;
}

@media (max-width: 1190px) {
  .container {
    width: 960px;
  }
  .main-section {
    min-height: 690px;
  }
  .lfet-wrapper img {
    width: 750px;
  }
  .lfet-wrapper__rectangle-blue {
    width: 500px;
    height: 200px;
  }
  .lfet-wrapper__rectangle-orange {
    width: 500px;
    height: 200px;
  }
  .lfet-wrapper__rectangle-grey {
    width: 500px;
    height: 200px;
  }
  .right-wrapper {
    right: 15px;
  }
  .main-section__circle {
    right: 50px;
  }
  .main-section__second-circle {
    top: 500px;
  }
  .cards-item__img {
    height: 85%;
  }
  .cards-item__text {
    font-size: 30px;
    top: 45%;
    left: 45%;
  }
  .second-section {
    min-height: 620px;
  }
  .second-section__title {
    margin: 30px;
  }
  .second-section__cards-item {
    margin-right: 10px;
  }
  .second-section__container {
    padding-top: 0;
  }
  .second-section__second-circle {
    right: 800px;
  }
  .second-section__icon-box {
    scale: 0.9;
  }
  .third-section__container {
    padding: 20px 0 0 0;
    margin-bottom: 20px;
  }
  .footer__container {
    position: relative;
    min-height: 200px;
  }
  .footer__img {
    scale: 0.7;
    left: -160px;
    bottom: -41px;
  }
}
@media (max-width: 991px) {
  .container {
    width: 720px;
  }
  .right-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .right-wrapper__text {
    background: rgba(153, 153, 153, 0.2549019608);
    border-radius: 20px;
    padding: 10px;
    backdrop-filter: blur(5px);
  }
  .right-wrapper__button {
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
  }
  .buttons__icon-btn {
    flex-direction: row-reverse;
    gap: 16px;
  }
  .cards-item__text {
    left: 40%;
  }
  .cards-item__img {
    position: absolute;
    bottom: 0;
    left: -20px;
  }
  .second-section__icon-box {
    justify-content: center;
  }
  .third-section {
    min-height: 520px;
  }
  .third-section__left-title {
    display: none;
  }
  .third-section__right-wrapper {
    padding: 20px;
    max-width: 680px;
    display: flex;
    flex-direction: column;
    z-index: 999;
  }
  .title {
    font-size: 42px;
  }
  .title__hr_top {
    bottom: 0;
  }
  .buttons__main-btn {
    font-size: 16px;
    width: 175px;
  }
}
@media (max-width: 767px) {
  .container {
    width: 540px;
  }
  .nav-menu {
    position: fixed;
    top: 0;
    right: -100%;
    height: 100vh;
    width: 50%;
    background: rgba(153, 153, 153, 0.4509803922);
    backdrop-filter: blur(5px);
    z-index: 9999;
    transition: 0.6s all;
  }
  .nav-menu__list {
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100%;
  }
  .nav-menu__list-item {
    margin-right: 0;
    font-size: 2rem;
  }
  .hamburger {
    width: 30px;
    position: fixed;
    top: 40px;
    right: 30px;
    height: 14px;
    z-index: 9999;
  }
  .hamburger span {
    height: 2px;
    width: 100%;
    display: block;
    background: black;
    margin-bottom: 4px;
    transition: 1s all;
  }
  .hamburger span:last-child {
    margin-bottom: 0px;
  }
  .hamburger_active span {
    margin-bottom: -2px;
  }
  .hamburger_active span:nth-child(1) {
    transform: translateY(3px) rotate(-45deg);
  }
  .hamburger_active span:nth-child(2) {
    display: none;
  }
  .hamburger_active span:nth-child(3) {
    transform: translateY(3px) rotate(45deg);
  }
  .nav-menu_active {
    right: 0%;
  }
}
@media (max-width: 575px) {
  .container {
    width: 100%;
  }
}